
import BScroll from 'better-scroll'
import { getRect } from '../../src/assets/js/dom'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'

import swipeoutButton from '../components/vux/swipeout-button'
export default {
name: 'invoices',
components: { Swipeout, SwipeoutItem, swipeoutButton },
inject: ['reload'], // 
data () {
return {
searchKey: '',
selectedType: { // 
key: 'all',
value: ''
},
selectedDate: '',
isShowTypes: false,
types: [ // 
{key: 'all', value: ''},
{key: 'pending', value: ''},
{key: 'success', value: ''},
{key: 'fail', value: ''}
],
page: 0, // 
invoiceList: [ // 
],
startY: 0,
refreshDelay: 20,
pullUpDirty: true, // 
pullUpLoad: false, // 
isPullUpLoad: false,
loading: false
}
},
computed: {
pullUpTxt () {
return this.pullUpDirty ? '' : ''
},
refreshTxt () {
return ''
}
},
watch: {
invoiceList: {
handler (newValue, oldValue) {
setTimeout(() => {
this.forceUpdate()
}, this.refreshDelay)
},
deep: true
}
},
mounted () {
this.getInvoiceList()



},
destroyed () {
this.$refs.scroll && this.$refs.scroll.destroy()
},
methods: {

getInvoiceList () {
this.loading = true
setTimeout(() => {
this.loading = false
this.invoiceList = this.invoiceList.concat([
{
date: '201811',
list: [
{
invoiceId: 1,
companyName: '',
typeKey: 'fail',
typeValue: '',
amount: '235',
payment: '',
dateTime: '2018.04.05'
},
{
invoiceId: 2,
companyName: '',
typeKey: 'pending',
typeValue: '',
amount: '235',
payment: '',
dateTime: '2018.04.05'
},
{
invoiceId: 3,
companyName: '',
typeKey: 'success',
typeValue: '',
amount: '235',
payment: '',
dateTime: '2018.04.05'
}
]
},
{
date: '201811',
list: [
{
invoiceId: 4,
companyName: '',
typeKey: 'success',
typeValue: '',
amount: '235',
payment: '',
dateTime: '2018.04.05'
},
{
invoiceId: 5,
companyName: '',
typeKey: 'success',
typeValue: '',
amount: '235',
payment: '',
dateTime: '2018.04.05'
},
{
invoiceId: 6,
companyName: '',
typeKey: 'success',
typeValue: '',
amount: '235',
payment: '',
dateTime: '2018.04.05'
}
]
}
])
this.page = this.page + 1
if (this.page >= 3) {
this.pullUpLoad = false
this.pullUpDirty = false
} else {
this.pullUpDirty = true
this.pullUpLoad = true
}

this.$nextTick(() => {
this.initScroll()
})
}, 2000)




},

initScroll () {
if (!this.$refs.wrapper) {
return
}
if (this.$refs.wrapper && this.pullUpLoad) {
this.$refs.wrapper.style.minHeight = `${getRect(this.$refs.wrapper).height + 1}px`
}
let options = {
probeType: 1,
click: true,
scrollbar: false,
pullUpLoad: {
threshold: 250
},
startY: this.startY,
freeScroll: false,
mouseWheel: false,
bounce: true,
zoom: false
}
this.scroll = new BScroll(this.$refs.wrapper, options)

this.scroll.on('scrollEnd', (pos) => {
this.$emit('scrollEnd', pos)
console.log(pos)
this.startY = pos.y
})

if (this.pullUpLoad) {
this._initPullUpLoad()
}
},

refresh () {
this.scroll && this.scroll.refresh()
},
forceUpdate () {
if (this.isPullUpLoad) {
this.isPullUpLoad = false
this.scroll.finishPullUp()
this.refresh()
} else {
this.refresh()
}
},
_initPullUpLoad () {
this.scroll.on('pullingUp', () => {
this.isPullUpLoad = true
this.getInvoiceList()
this.$emit('pullingUp')
})
},
showTypes () {
this.isShowTypes = true
},
selectType (type) {
this.selectedType = type
this.isShowTypes = false
},
handleEvents (type) {
console.log('event: ', type)
},
clearInput () {
this.searchKey = ''
},
refreshList () {
this.reload()
},
showDetail (invoiceId) {
this.$router.push('/invoice/' + invoiceId)
},

deleteInvoiceById (invoiceId) {
this.invoiceList.some((invoiceData, dataIndex) => {
return invoiceData.list.some((invoice, invoiceIndex) => {
if (invoice.invoiceId === invoiceId) {
this.invoiceList[dataIndex].list.splice(invoiceIndex, 1)
return true
}
})
})
},
onButtonClick (args) {
var vm = this
this.$vux.confirm.show({
title: '',
content: '<br/>' + '',
dialogTransition: 'vux-dialog',
onCancel () {
args.parent.onItemClick()
},
onConfirm () {
args.parent.onItemClick()
let invoiceId = args.primaryKey

setTimeout(() => {
vm.deleteInvoiceById(invoiceId)
}, 2000)
}
})
},
showDateTime () {
var vm = this
this.$vux.datetime.show({
cancelText: '',
confirmText: '',
format: 'YYYY-MM',
yearRow: '{value}',
monthRow: '{value}',
value: vm.selectedDate, //  props
onConfirm (val) {
vm.selectedDate = val
},
onHide () {
console.log('hide')
},
onShow () {
console.log('show')
}
})
}
}
}



